import React from "react";
import {connect} from "react-redux";

import OraCheckoutAssistModePopup from "./OraCheckoutAssistModePopup";
import OraCheckoutAssistModeConfirmationPopup from "./OraCheckoutAssistModeConfirmationPopup";
import {getAssistModeStateData, isLoadingAssistModeState, getAssistModeConfirmationModalStateData, getAssistModeConfirmationLeaveModalStateData, getSelectedEmployee} from "../../selectors";
import {getAssistModeState, openAssistModeModal, openConfirmationLeaveModal, closeConfirmationLeaveModal, setAssistEmployee, closeConfirmationModal, leaveAssistMode} from "eshop/modules/checkout/actions/assistMode";
import {OraButton, OraLoader} from "eshop/components/OraCommonComponents";
import {
    getFtthAvailabilityDateFromOffers,
    getMigrationFtthAvailabilityAttribute
} from "../../../fix/selectors/root";
import {getMigrationFtthAvailabilityDate} from "../../../magnum2/selectors/migration";
import {getMigrationFtthAvailabilityDateFromMagnum} from "../../../magnum2/selectors";
import AssistMode from "../consents/AssistModeEnum";

class OraCheckoutAssistModePanel extends React.Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this.props.getAssistMode();
        OPL.UI.loadModules(document.getElementById("assist-mode-panel-loader-component"), {
            path: "core/modules/loader",
            options: "{\"fadeDuration\":120}",
        });
    }

    openAssistModeLeaveConfirmationPopup() {
        this.props.openConfirmationLeaveModal();
    }

    refresh(){
        this.props.getAssistMode();
    }

    openAssistModePopup() {
        this.props.openAssistModeModal();
    }

    onRejectActivate(){
        this.props.closeConfirmationModal();
        // ugly but not working without timeouts...
        setTimeout(this.props.openAssistModeModal.bind(this),400);
    }

    onConfirmActivate(){
        this.props.setAssistEmployee({sisId: this.props.selectedEmployee});
    }

    onRejectLeave(){
        this.props.closeConfirmationLeaveModal();
    }

    onConfirmLeave(){
       this.props.leaveAssistMode();
    }

    _renderInternal() {
        let state = this.props.assistModeState.state;
        let assistedEmployee = this.props.assistModeState.assistedEmployee || {};
        let loggedEmployee = this.props.assistModeState.loggedEmployee || {};
        if (this.props.ftthAvailabilityDate && state === AssistMode.INACTIVE) {
            return this._renderForUnavailableAssistState();
        }
        if (state === AssistMode.ACTIVE) {
            return this._renderForActiveState(assistedEmployee);
        } else if (state === AssistMode.INACTIVE) {
            return this._renderForInactiveState(loggedEmployee);
        } else if (state === AssistMode.ERROR) {
            return this._renderForErrorState();
        } else {
            return this._renderForUnknownState();
        }
    }

    _renderForActiveState(assistedEmployee) {
        return (
            <div className="l-table-row__wrapper no-table-row-small">
                <div className="l-row l-table-row">
                    <div className="l-col l-col-2 l-col-small-12 u-vertical-middle u-font-bold">
                        <span>{this.props.descriptions.mode}&nbsp;</span>
                        <span className="g-green3-c">{this.props.descriptions.stateActiveLabel}</span>
                    </div>
                    <div className="l-col l-col-3 l-col-small-12 u-vertical-middle u-font-bold">
                        <span>{this.props.descriptions.asName}&nbsp;</span>
                        <span className="g-green3-c">
                            {
                                assistedEmployee.firstName + " " +
                                assistedEmployee.lastName + " (" +
                                assistedEmployee.loginOtsa + ")"
                            }
                        </span>
                    </div>
                    <div className="l-col l-col-2 l-col-small-12 u-vertical-middle u-font-bold">
                        <span>{this.props.descriptions.bscs}&nbsp;</span>
                        <span className="g-green3-c">
                            {assistedEmployee.fullBscs || ""}
                        </span>
                    </div>
                    <div className="l-col l-col-2 l-col-small-12 u-vertical-middle u-font-bold u-large-padding-left-l u-medium-padding-left-l u-large-no-padding-r u-medium-no-padding-r">
                        <div>
                        {this.props.descriptions.channel}&nbsp;
                        <span className="g-green3-c">
                            {assistedEmployee.salesChannelName || ""}
                        </span>
                        </div>
                        <div className={"u-large-padding-top-s u-medium-padding-top-s"}>
                            {this.props.descriptions.sisChannel || "Kanał SIS:"}&nbsp;
                            <span className="g-green3-c">
                            {assistedEmployee.sisChannelDescription || ""}
                        </span>
                        </div>
                    </div>
                    <div className="l-col l-col-3 l-col-small-12 u-vertical-middle">
                        <OraButton className="u-right" onClick={this.openAssistModeLeaveConfirmationPopup.bind(this)}
                                   type="primary">
                            {this.props.descriptions.leaveAssistModeButtonLabel}
                        </OraButton>
                    </div>
                </div>
            </div>
        )
    }

    _renderForInactiveState(loggedEmployee) {
        return (
            <div className="l-table-row__wrapper no-table-row-small">
                <div className="l-row l-table-row">
                    <div className="l-col l-col-2 l-col-small-12 u-vertical-middle u-font-bold">
                        <span>{this.props.descriptions.mode}&nbsp;</span>
                        <span className="g-redf-c">{this.props.descriptions.stateInactiveLabel}</span>
                    </div>
                    <div className="l-col l-col-3 l-col-small-12 u-vertical-middle u-font-bold">
                        <span>{this.props.descriptions.assistModeInactiveEmployeeDescription}</span>
                    </div>
                    <div className="l-col l-col-2 l-col-small-12 u-vertical-middle u-font-bold">
                        <span>{this.props.descriptions.bscs}&nbsp;</span>
                        <span className="g-green3-c">
                            {loggedEmployee.fullBscs || ""}
                        </span>
                    </div>
                    <div className="l-col l-col-2 l-col-small-12 u-vertical-middle u-font-bold u-large-padding-left-l u-medium-padding-left-l u-large-no-padding-r u-medium-no-padding-r">
                        <div>
                            {this.props.descriptions.channel}&nbsp;
                            <span className="g-green3-c">
                            {loggedEmployee.salesChannelName || ""}
                        </span>
                        </div>
                        <div className={"u-large-padding-top-s u-medium-padding-top-s"}>
                            {this.props.descriptions.sisChannel || "Kanał SIS:"}&nbsp;
                            <span className="g-green3-c">
                            {loggedEmployee.sisChannelDescription || ""}
                        </span>
                        </div>
                    </div>
                    <div className="l-col l-col-3 l-col-small-12 u-vertical-middle">
                        <OraButton className="u-right" onClick={this.openAssistModePopup.bind(this)} type="primary">
                            {this.props.descriptions.enterAssistModeButtonLabel}
                        </OraButton>
                    </div>
                </div>
            </div>
        )
    }

    _renderForUnknownState() {
        return (
            <div className="l-table-row__wrapper">
                <div className="l-row l-table-row">
                    <div className="l-col l-col-12 l-col-small-12 u-vertical-middle u-text-center u-font-bold">
                        {this.props.descriptions.unknownStateLabel}
                    </div>
                </div>
            </div>
        )
    }

    _renderForErrorState() {
        return (
            <div className="l-table-row__wrapper">
                <div className="l-row l-table-row">
                    <div className="l-col l-col-10 l-col-small-12 u-vertical-middle g-redf-c u-font-bold">
                        {this.props.errorDesc || this.props.descriptions.defaultErrorText}
                    </div>
                    <div className="l-col l-col-2 l-col-small-12 u-vertical-middle">
                        <OraButton className="u-right" onClick={this.refresh.bind(this)} type="primary">
                            {this.props.descriptions.btnRefreshLabel}
                        </OraButton>
                    </div>
                </div>
            </div>
        )
    }

    _renderForUnavailableAssistState() {
        return (
            <div className="l-table-row__wrapper">
                <div className="l-row l-table-row">
                    <div className="l-col l-col-12 l-col-small-12 u-vertical-middle g-redf-c u-font-bold">
                        {this.props.descriptions.unavailableAssistModeText}
                    </div>
                </div>
            </div>
        )
    }

    render() {
        return (
            <OraLoader className="l-page-row u-padding-s u-padding-top-s g-gray2-bg"
                       loading={this.props.isLoadingAssistModeState} id="assist-mode-panel-loader-component"
                       parentId="assist-mode-loaders" size='m'>
                <div className="l-row">
                    <div className="l-col l-col-12">
                        {this._renderInternal()}
                    </div>
                </div>
                <OraCheckoutAssistModePopup descriptions={this.props.descriptions}/>
                <OraCheckoutAssistModeConfirmationPopup descriptions={this.props.descriptions}
                                                        id={"ora-checkout-assist-mode-confirmation-modal"}
                                                        open={this.props.assistModeConfirmationModalState}
                                                        onReject={this.onRejectActivate.bind(this)}
                                                        onConfirm={this.onConfirmActivate.bind(this)}/>
                <OraCheckoutAssistModeConfirmationPopup descriptions={this.props.descriptions}
                                                        id={"ora-checkout-assist-mode-leave-confirmation-modal"}
                                                        open={this.props.assistModeConfirmationLeaveModalState}
                                                        onReject={this.onRejectLeave.bind(this)}
                                                        onConfirm={this.onConfirmLeave.bind(this)}/>
            </OraLoader>
        );
    }
}

const mapDispatchToProps = dispatch => ({
    getAssistMode: () => dispatch(getAssistModeState()),
    openAssistModeModal: () => dispatch(openAssistModeModal()),
    closeConfirmationModal: () => dispatch(closeConfirmationModal()),
    openConfirmationLeaveModal: () => dispatch(openConfirmationLeaveModal()),
    closeConfirmationLeaveModal: () => dispatch(closeConfirmationLeaveModal()),
    setAssistEmployee: (employee) => dispatch(setAssistEmployee(employee)),
    leaveAssistMode: () => dispatch(leaveAssistMode()),
});

const mapStateToProps = state => ({
    assistModeState: getAssistModeStateData(state),
    isLoadingAssistModeState: isLoadingAssistModeState(state),
    assistModeConfirmationModalState: getAssistModeConfirmationModalStateData(state),
    assistModeConfirmationLeaveModalState: getAssistModeConfirmationLeaveModalStateData(state),
    selectedEmployee: getSelectedEmployee(state),
    ftthAvailabilityDate: getMigrationFtthAvailabilityDate(state) || getMigrationFtthAvailabilityAttribute(state) || getFtthAvailabilityDateFromOffers(state) || getMigrationFtthAvailabilityDateFromMagnum(state)
});

export default connect(mapStateToProps, mapDispatchToProps)(OraCheckoutAssistModePanel);
